var _self;
export default {
	data() {
		return {
			dataList: [],
			mescroll: null, //mescroll实例对象
			downOption:{
				auto:true // 不自动加载
			},
			upOption:{
				auto:false, // 不自动加载
				textLoading:"正在加载",
				textNoMore:"没有更多数据了",
				page: {
					num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
					size: 5 // 每页数据的数量
				},
				noMoreSize: 5, //如果列表已无数据,可设置列表的总数量要大于半页才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看; 默认5
				empty:{
					tip: '~ 空空如也 ~', // 提示
					btnText: '去首页'
				}
			},
		};
	},
	onLoad() {
		_self = this;
	},
	methods: {
		/*下拉刷新的回调 */
		downCallback(mescroll) {
			//联网加载数据
			this.getListDataFromNet(
				0,
				1,
				data => {
					//联网成功的回调,隐藏下拉刷新的状态
					mescroll.endSuccess();
					//设置列表数据
					this.dataList.unshift(data[0]);
				},
				() => {
					//联网失败的回调,隐藏下拉刷新的状态
					mescroll.endErr();
				}
			);
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		upCallback(mescroll) {
			//联网加载数据
			this.getListDataFromNet(mescroll.num, mescroll.size, (curPageData)=>{
				//联网成功的回调,隐藏下拉刷新和上拉加载的状态;
				// console.log("i="+this.i+", mescroll.num=" + mescroll.num + ", mescroll.size=" + mescroll.size + ", curPageData.length=" + curPageData.length);
				mescroll.endSuccess(curPageData.length);
				//设置列表数据
				if(mescroll.num == 1) this.dataList = []; //如果是第一页需手动制空列表
				this.dataList=this.dataList.concat(curPageData); //追加新数据
			}, () => {
				//联网失败的回调,隐藏下拉刷新的状态
				mescroll.endErr();
			})
		},
		/*联网加载列表数据
			在您的实际项目中,请参考官方写法: http://www.mescroll.com/uni.html#tagUpCallback
			请忽略getListDataFromNet的逻辑,这里仅仅是在本地模拟分页数据,本地演示用
			实际项目以您服务器接口返回的数据为准,无需本地处理分页.
			* */
		async getListDataFromNet(pageNum, pageSize, successCallback, errorCallback) {
			try {
				// const res = await this.$req.ajax({
				// 	path: this.$api.team.shop.shoplist,
				// 	data: {
				// 		goods_type: this.i + 1,
				// 		page: pageNum,
				// 		pagesize: pageSize
				// 	}
				// });
				// let { data, code } = res.data;
				// if (code == '1') {
					let listData = [{
					id: '',
					status: '正常', //正常
					number: '01', //编号
					aisle: [
						//通道
						{
							name: '通道1', //
							id: 1 //通道id
						}
					],
					term: '锦艺四季城', //项目名称
					region: '未央区', //所属区域
					position: '科学大道科学大道98号' //位置
				},
				{
					id: '',
					status: '故障', //正常
					number: '01', //编号
					aisle: [
						//通道
						{
							name: '通道1', //
							id: 1 //通道id
						}
					],
					term: '锦艺四季城', //项目名称
					region: '未央区', //所属区域
					position: '科学大道科学大道98号' //位置
				}];
					successCallback && successCallback(listData);
				// } else {
				// 	errorCallback && errorCallback();
				// }
			} catch (e) {
				console.log(e);
			}
		},
		onPlayerIcon(e){
			_self.$Router.push({ name: 'video', params: {} });
		}
		
	}
};
